<template>
    <img :src="a" class="round_icon">
    </img>
</template>

<script>
  /*这个头像有悬浮变大效果*/
    export default {
        name: "HeadPortrait",
        data() {
            return {
              // a:"http://yssssss.ticp.io:38314/blog/static/files/imgs/",
              a:"http://106.14.137.176:8080/blog/static/files/imgs/",
            }
        }, methods: {}, components: {}, mounted() {
        this.a=this.a+this.src
        },props:['src']
    }
</script>

<style scoped>
  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }
</style>



